import React, { useEffect, useRef } from "react";
import Sortable from "sortablejs";
import { IGetUser } from "../../requests/interfaces";
import {
  getFavoritesLS,
  setFavoritesLS,
} from "../../utils/local_storage_promises";
import "./StreamerList.css";

interface IProps {
  users: IGetUser[];
  id: "favorite-streamers" | "all-streamers";
}

const StreamerList = ({ users, id }: IProps) => {
  const listRef = useRef(null);

  useEffect(() => {
    if (id === "favorite-streamers") {
      new Sortable(listRef.current!, {
        group: "shared",
        animation: 150,
        chosenClass: "chosen",
        onAdd: async (e) => {
          if (e.newIndex !== undefined) {
            const favorites = await getFavoritesLS();
            const newFavorites = [...favorites];
            newFavorites.splice(e.newIndex, 0, e.item.id);
            await setFavoritesLS(newFavorites);
          }
        },
        onRemove: async (e) => {
          if (e.oldIndex !== undefined) {
            const favorites = await getFavoritesLS();
            const newFavorites = [...favorites];
            newFavorites.splice(e.oldIndex, 1);
            await setFavoritesLS(newFavorites);
          }
        },
        onEnd: async (e) => {
          if (
            e.to.id === id &&
            e.oldIndex !== undefined &&
            e.newIndex !== undefined
          ) {
            const favorites = await getFavoritesLS();
            const newFavorites = [...favorites];
            const oldElement = newFavorites.splice(e.oldIndex, 1)[0];
            newFavorites.splice(e.newIndex, 0, oldElement);
            await setFavoritesLS(newFavorites);
          }
        },
      });
    } else {
      new Sortable(listRef.current!, {
        group: "shared",
        animation: 150,
        chosenClass: "chosen",
        sort: false,
      });
    }
  }, [id]);

  return (
    <div className="list" ref={listRef} id={id}>
      {users.map((data) => {
        return (
          <div className="streamer" id={data.id}>
            <img className="profil-pic" src={data.profile_image_url} alt="" />
            <span className="streamer-name">{data.display_name}</span>
          </div>
        );
      })}
    </div>
  );
};

export default StreamerList;
