import React from "react";
import "./Logo.css";

interface IProps {
  iconWidth: 16 | 32 | 48 | 128;
  fontSize: number;
  marginBottom?: number;
  marginTop?: number;
  multiline?: boolean;
}

const Logo = ({
  iconWidth,
  fontSize,
  marginBottom = 0,
  marginTop = 0,
  multiline = false,
}: IProps) => {
  return (
    <div
      className="logo-container"
      style={{
        fontSize: `${fontSize}px`,
        marginBottom: `${marginBottom}px`,
        marginTop: `${marginTop}px`,
      }}
    >
      <img
        className="logo"
        style={{
          width: `${iconWidth}px`,
        }}
        src={`../images/icon-${iconWidth}.png`}
        alt="logo"
      />
      {multiline ? (
        <div className="multiline">
          <div>
            <span style={{ color: "#a970ff" }}>T</span>witch
          </div>
          <div>
            <span style={{ color: "#a970ff" }}>F</span>avorite
          </div>
          <div>
            <span style={{ color: "#a970ff" }}>S</span>treamers
          </div>
        </div>
      ) : (
        <span>Twitch Favorite Streamers</span>
      )}
    </div>
  );
};

export default Logo;
