import React, { useEffect, useState } from "react";
import Button from "../../components/Button";
import Loader from "../../components/Loader";
import Logo from "../../components/Logo";
import {
  FetchAllFollowedInput,
  FetchAllFollowedResult,
  LoginInput,
  LoginResult,
  LogoutInput,
  LogoutResult,
  Message,
  Query,
} from "../../interfaces";
import { IGetUser } from "../../requests/interfaces";
import {
  getFavoritesLS,
  getValueLS,
  setFavoritesLS,
  setValueLS,
} from "../../utils/local_storage_promises";
import "./Options.css";
import StreamerList from "./StreamerList";

const Options = () => {
  const [allInfos, setAllInfos] = useState<IGetUser[]>([]);
  const [favoriteInfos, setFavoriteInfos] = useState<IGetUser[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>();
  const [mustLogin, setMustLogin] = useState<boolean>();
  const [notification, setNotification] = useState<boolean>();

  useEffect(() => {
    getValueLS<string>("notification").then((n) => {
      if (n === undefined) {
        setValueLS("notification", true);
        setNotification(true);
      } else {
        setNotification(Boolean(n));
      }
    });
    fetchAllFollowed();
  }, []);

  const handleNotifications = () => {
    setValueLS("notification", !notification);
    setNotification(!notification);
  };

  const handleLogin = async () => {
    const afterResponse = (res: LoginResult) => {
      if (res === Message.SUCCESS) {
        fetchAllFollowed();
      } else {
        setMustLogin(true);
        setIsLoading(false);
      }
    };
    setIsLoading(true);
    if (process.env.REACT_APP_PLATFORM === "chrome") {
      chrome.runtime.sendMessage<LoginInput, LoginResult>(
        Query.LOGIN,
        (res) => {
          afterResponse(res);
        }
      );
    } else {
      const res = await browser.runtime.sendMessage<LoginInput, LoginResult>(
        Query.LOGIN
      );
      afterResponse(res);
    }
  };

  const handleLogout = async () => {
    const afterResponse = () => {
      setIsLoading(false);
      setMustLogin(true);
      setAllInfos([]);
      setFavoriteInfos([]);
    };
    setIsLoading(true);
    if (process.env.REACT_APP_PLATFORM === "chrome") {
      chrome.runtime.sendMessage<LogoutInput, LogoutResult>(
        Query.LOGOUT,
        () => {
          afterResponse();
        }
      );
    } else {
      await browser.runtime.sendMessage<LogoutInput, LogoutResult>(
        Query.LOGOUT
      );
      afterResponse();
    }
  };

  const fetchAllFollowed = async () => {
    setIsLoading(true);
    const afterResponse = async (res: FetchAllFollowedResult) => {
      if (res.message === Message.FETCH_ERROR) {
        setIsLoading(false);
        return;
      }
      if (
        res.message === Message.MUST_LOGIN ||
        res.message === Message.FAILURE
      ) {
        setIsLoading(false);
        setMustLogin(true);
        return;
      }
      const followedUsersInfo = res.followedUsersInfo || [];
      const favStreams = await getFavoritesLS();
      const cleanFav = favStreams.filter((f) =>
        followedUsersInfo.find((a) => a.id === f)
      );
      await setFavoritesLS(cleanFav);
      setFavoriteInfos(
        followedUsersInfo
          .filter((a) => cleanFav.includes(a.id))
          .sort((a, b) => cleanFav.indexOf(a.id) - cleanFav.indexOf(b.id))
      );
      setAllInfos(
        followedUsersInfo
          .filter((a) => !cleanFav.includes(a.id))
          .sort((a, b) => a.login.localeCompare(b.login))
      );
      setIsLoading(false);
      setMustLogin(false);
    };
    if (process.env.REACT_APP_PLATFORM === "chrome") {
      chrome.runtime.sendMessage<FetchAllFollowedInput, FetchAllFollowedResult>(
        Query.FETCH_ALL_FOLLOWED,
        async (res) => {
          await afterResponse(res);
        }
      );
    } else {
      const res = await browser.runtime.sendMessage<
        FetchAllFollowedInput,
        FetchAllFollowedResult
      >(Query.FETCH_ALL_FOLLOWED);
      await afterResponse(res);
    }
  };

  const followedTitle = (
    <h1 className="title">{chrome.i18n.getMessage("followedStreamers")}</h1>
  );
  const favoritesTitle = (
    <h1 className="title">{chrome.i18n.getMessage("favoriteStreamers")}</h1>
  );
  const loginButton = (
    <Button onClick={handleLogin} text={chrome.i18n.getMessage("login")} />
  );
  const logoutButton = (
    <Button onClick={handleLogout} text={chrome.i18n.getMessage("logout")} />
  );
  const notifOn = (
    <img
      className="notifications"
      src="../images/notifications.png"
      alt="notifications"
    />
  );
  const notifOff = (
    <img
      className="notifications"
      src="../images/notifications-off.png"
      alt="notifications-off"
    />
  );

  return (
    <main className="main">
      {isLoading ? (
        <Loader />
      ) : (
        <>
          {mustLogin ? (
            <div className="container full">
              <Logo iconWidth={128} fontSize={30} marginBottom={10} multiline />
              {loginButton}
            </div>
          ) : (
            <div className={"container"}>
              <div className="row margin height">
                <Logo iconWidth={48} fontSize={25} />
                {logoutButton}
              </div>
              <div className="lists">
                <div className="listContainer">
                  {followedTitle}
                  <StreamerList users={allInfos} id={"all-streamers"} />
                </div>
                <div className="separator" />
                <div className="listContainer">
                  <div className="row">
                    {favoritesTitle}
                    <div
                      className="notifications-container"
                      onClick={handleNotifications}
                    >
                      {notification ? notifOn : notifOff}
                    </div>
                  </div>
                  <StreamerList
                    users={favoriteInfos}
                    id={"favorite-streamers"}
                  />
                </div>
              </div>
            </div>
          )}
        </>
      )}
    </main>
  );
};

export default Options;
